/* Style de base pour la timeline */
.section-produre {
  height: 100vh;
}
/* Style des étapes de la timeline */
.step {
  display: flex;
  align-items: center;
  width: 100%;
  justify-content: center;
}
.title-service {
  margin-bottom: 50px;

  font-size: 2rem;
  text-align: center;
}
.step-number {
  background-color: #000000d4;
  color: #fff;
  display: flex;
  margin-right: 3vw;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  font-weight: bold;
}
.timeline-container{
  height : 100vh;
}
.step-content {
  border-left: 2px solid #000000d4;
  padding: 0 0 3vw 3vw;
  width: 70%;}

.step h3 {
  margin-bottom: 5%;
  font-weight: 600;
}

.step p {
  margin: 0;
  color: #555;
}
