.form-section {
  background-image: url("../../assets/images/background3.jpg");
}
.contact-container {
  align-items: center;
}
.top-form {
  display: flex;
  flex-direction: column;
  justify-content: start;
  flex-wrap: wrap;
}
textarea {
  box-shadow: 0 8px 4px rgb(0 0 0 / 24%);
}
.form-section select {
  background-color: #fff;
  color: #333;
  padding: 5px 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-bottom: 15px;
}
input {
  box-shadow: 0 8px 4px rgb(0 0 0 / 24%);
}
.form-section h2 {
  color: #333;
  padding: 0 2% 5% 2%;

  text-align: center;
}

.form-section label {
  color: #333;
  display: block;
  margin-bottom: 5px;
}

.form-section input[type="text"],
.form-section input[type="email"],
.form-section input[type="text"],
.form-section input[type="file"],
.form-section textarea {
  background-color: #fff;
  color: #333;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 15px;
  width: 100%;
}
form {
  background-color: #fff;
  color: #333;
  padding: 20px;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
  width: 55%;
}
.contact-infos {
  background-color: #000;
  color: white;
  padding: 20px;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
  width: 35%;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.div-content {
  height: 75%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
.submit {
  background-color: #333;
  color: #fff;
  border-radius: 5px;
  padding: 10px 20px;
  cursor: pointer;
  transition: all 0.3s ease;
  border: 1px solid white;
}

.submit:hover {
  background-color: #555; /* Nouvelle couleur au survol, un ton plus foncé */
}
@media screen and (max-width: 1200px) {
  .top-form {
  }

  .contact-container > * {
    width: 85%;
  }
}
.email-link {
  background-image: linear-gradient(to right, #54b3d6, #54b3d6 50%, #fff 50%);
  background-size: 200% 100%;
  background-position: -100%;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  transition: all 0.3s ease-in-out;
}
.email-link:before {
  content: "";
  background: #54b3d6;
  display: block;
  position: absolute;
  bottom: -3px;
  left: 0;
  width: 0;
  height: 3px;
}
.email-link:hover {
  background-position: 0;

  cursor: pointer;
}
.email-link:hover::before {
  width: 100%;
}
.label-file-upload {
  border: 1px solid #ccc;
  border-radius: 5px;
  width: fit-content;
  padding: 1%;
  transition: transform 0.5s ease-in-out;
}
.label-file-upload:hover {
  transform: scale(1.1);
  cursor: pointer;
  background-color: #ccc;
}
.uploaded-files-list {
  background-color: #fff;
  color: #333;
  font-weight: bold;
  border-radius: 5px;
  padding: 10px;

  width: 100%;
  /*     box-shadow: 0 8px 4px rgb(0 0 0 / 24%);
 */
}
